import React from "react";
const About = props => {
  //console.log(props);
  return (
    <div>
      <p>This is about, {props.name}</p>
      <iframe
        title="anIframe"
        className="ytVideo"
        width="560"
        height="315"
        src="https://www.youtube.com/embed/Gj6CIveQGLs"
        allowFullScreen
      />
    </div>
  );
};

export default About;
