import React, { Component } from "react";

class Father extends Component {
  state = {};
  fatherClicked = e => {
    console.log("fatherw as clicked", e);
  };
  render() {
    return (
      <div onClick={this.fatherClicked}>
        <h1>Hi I'm father component</h1>
        {this.props.children}
      </div>
    );
  }
}

export default Father;
