import React, { Component } from "react";
import axios from "axios";
import "./Posts.css";

class Posts extends Component {
  state = { posts: [] };

  componentDidMount() {
    // get posts
    setTimeout(async () => {
      const posts = await this.getPosts();
      this.setState({ posts: posts });
    }, 5000);
  }

  getPosts = async () => {
    try {
      const { data: posts } = await axios.get(
        "https://jsonplaceholder.typicode.com/posts"
      );
      //console.log(posts);
      return posts;
    } catch (e) {
      console.log("something went wrong...", e);
    }
    return [];
  };

  render() {
    let count = 0;
    return (
      <div>
        {this.state.posts.length > 0 ? (
          <table className="fixed_headers">
            <thead>
              <tr>
                <th>User ID</th>
                <th>Post ID</th>
                <th>Title</th>
                <th>Body</th>
              </tr>
            </thead>
            <tbody>
              {this.state.posts.map(post => (
                <tr key={count++}>
                  <td>{post.userId}</td>
                  <td>{post.id}</td>
                  <td>{post.title}</td>
                  <td>{post.body}</td>
                </tr>
              ))}
            </tbody>
          </table>
        ) : (
          <img src="./loading.gif" height="600px" alt="loading..." />
        )}
      </div>
    );
  }
}

export default Posts;
