import React, { Component } from "react";
import "./Filters.css";

class Filters extends Component {
  state = { show: false, name: "your name", tempName: "" };
  showFilters = () => {
    let show = this.state.show;
    show = !show;
    this.setState({ show });
  };
  handleSubmit = () => {
    this.setState({ name: this.state.tempName });
    this.showFilters();
  };
  handleChange = e => {
    this.setState({ tempName: e.target.value });
  };
  filterBox = () => (
    <div>
      <div className="divBox">
        <div className="divBox2">
          <label htmlFor="name">Name</label>
          <input
            type="text"
            value={this.state.tempName}
            onChange={this.handleChange}
          />
          <input type="button" value="OK" onClick={this.handleSubmit} />
        </div>
      </div>
    </div>
  );
  render() {
    return (
      <div>
        <div>
          Filters these are... click me,{" "}
          <span style={{ color: "salmon" }}>{this.state.name}</span>!
          <button onClick={this.showFilters}>me</button> to show/hide Filters
        </div>
        {this.state.show ? this.filterBox() : null}
      </div>
    );
  }
}

export default Filters;
