import React, { Component } from "react";

class Son extends Component {
  state = {};
  sonClicked = e => {
    e.stopPropagation();
    console.log("son was clicked", e);
  };
  render() {
    return (
      <div>
        <h2 onClick={this.sonClicked}>Hi i'm son component</h2>
      </div>
    );
  }
}

export default Son;
